'use strict';

var Component = require('../../../ui-base/component');
var template = require('./index.html');
var Menu = require('../menu/index.js');

/**
 * @class Sidebar
 * @extend Component
 * @param {object}        [options.data]                          = 
 * @param {string}        [options.data.class]                    => class
 * @param {array}         [options.data.menus]                    => 
 * @param {string}        [options.data.top='60px']               => style top
 * @param {boolean}       [options.data.active=true]              => 
 * @param {string}        [options.data.bodyEl='']                => bodyid,,bodyEl
 * @param {boolean}       [options.data.uniqueOpened=true]        => 
 * @param {string}        [options.data.titleKey=title]           => key
 * @param {string}        [options.data.urlKey="url"]             => key
 * @param {string}        [options.data.pageKey="title"]          => key
 * @param {string}        [options.data.childrenKey="children"]   => key
 */
var Sidebar = Component.extend({
  name: 'ui.sidebar',
  template: template,
  /**
   * @protected
   */
  config: function() {
    this.defaults({
      class: '',
      uniqueOpened: true,
      menus: [],
      titleKey: 'title',
      urlKey: 'url',
      pageKey: 'title',
      childrenKey: 'children',
      top: '60px',
      active: true,
      bodyEl: '',
    });

    this.supr();
  },
  initBodyEl: function() {
    if (this.data.bodyEl) {
      this.data.$bodyEl = document.getElementById(this.data.bodyEl);
      if (this.data.$bodyEl) {
        this.data.$bodyEl.style.transition = 'left .3s';
      }
    }
  },
  toggle: function() {
    this.initBodyEl();

    this.data.active = !this.data.active;
    if (this.data.$bodyEl) {
      this.data.$bodyEl.style.left = this.data.active? '180px' : '0';
    }
  },
});

Sidebar.directive('top', function(ele, value) {
  this.$watch(value, function(top) {
    ele.style.top = top;
  });
});

module.exports = Sidebar;


