'use strict';

var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');

/**
 * @class KLIcon
 * @extend Component
 * @param {object}          [options.data]                        = 
 * @param {string}          [options.data.class]                  => class
 * @param {string}          [options.data.type]                  => class
 * @param {string}          [options.data.fontSize]                  => 
 * @param {string}          [options.data.color]                  => 
 */
var KLIcon = Component.extend({
    name: 'kl-icon',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            type: '',
            fontSize: '',
            color: ''
        });
        this.supr();
    },
    onClick: function (e) {
        this.$emit('click', e);
    }
});

module.exports = KLIcon;


