'use strict';

var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');

/**
 * @class Card
 * @extend Component
 * @param {object}          [options.data]                        = 
 * @param {string}          [options.data.class]                  => class
 * @param {boolean}          [options.data.isShowLine]            => title
 * @param {boolean}          [options.data.isShowBtLine]          => title
 * @param {boolean}          [options.data.isIndent]              => title
 */
var Card = Component.extend({
    name: 'ui.card',
    template: template,
    $tools: null,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            title: '',
            isShowLine: true,
            isShowBtLine: false,
            isIndent: true,
        });
        this.supr();

    }
});

module.exports = Card;


