'use strict';

var Component = require('../../../ui-base/component');
var template = require('./index.html');
var _ = require('../../../ui-base/_');

/**
 * @class KLCrumb
 * @extend Component
 * @param {object}          [options.data]                     = 
 * @param {string}          [options.data.class]               => class
 * @param {string}          [options.data.separator]           => 
 * @param {string}          [options.data.class]               => kl-crumb-itemclass
 * @param {string}          [options.data.href]                => kl-crumb-item
 */
var KLCrumb = Component.extend({
    name: 'kl-crumb',
    template: template,
    /**
     * @protected
     */
    config: function() {
        _.extend(this.data, {
            separator: '/',
            crumbArr: []
        });
        this.supr();
    }
});

module.exports = KLCrumb;


